import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    count: 0,
    click_item_num: 0,
    status: false, // 记录当前的登录状态
    list: [],
    note_username: '',
    note_password: ''
  },
  mutations: {
    upp () {
      // this.state.count++
      store.state.count++
    },
    clearAll () {
      store.state.count = 0
      store.state.list = []
      store.state.click_item_num = 0
      localStorage.clear()
      console.log(store.state.list)
    },
    register1 (state, value) {
      store.state.note_username = value.regname
      store.state.note_password = value.regpass
    },
    changeStatus (state) {
      state.status = !state.status
    },
    addItem (state, value) {
      // console.log(this.state)
      if (value.note_title === '' || value.note_content === '') {
        alert('请补全内容')
      } else {
        state.list.unshift(value)
        console.log(state.list)
      }
    },
    updateItem (state, value) {
      // console.log(this.state)
      state.list[state.click_item_num] = value
      console.log(state.list)
    }
  },
  actions: {
  }
})
export default store



