// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import store from './store/store'
import router from './router'
import './assets/styles/reset.css'
import './assets/styles/border.css'
import App from './App'

Vue.config.productionTip = false

// 注册doubletap事件
var VueTouch = require('vue-touch')
VueTouch.registerCustomEvent('doubletap', {
  type: 'tap',
  taps: 2
})

Vue.use(VueTouch, {name: 'v-touch'})

/* eslint-disable no-new */
new Vue({ // 使用vue-router时，要最后挂载App根实例，不然会导致配置不成功!
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})
router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) { // 需要验证登录状态的页面路由
    // 这里判断用户是否登录，验证本地存储是否有token
    if (!store.state.status && to.path !== '/') { // 未登录,原路由却指向非登录页
      next('/') // 重定向到登录页
      console.log('11' + store.state.status)
    } else if (store.state.status && to.path === '/') { // 已登录,原路由却指向登录页
      next('/home/list') // 重定向到主页
      console.log('22' + store.state.status + to.path)
    } else {
      console.log('33' + store.state.status + to.path)
      next() // 【必须有！！！否则当不满足以上2条件，页面将无法进行转向】
    }
  } else {
    console.log('44')
    next() // 确保一定要调用 next()
  }
})



