<template>
  <div id="home-list">
    <p>HomeList组件</p>
    <ul class="list">
      <v-touch
        v-for="(item, index) of pageList"
        :key="index"
        tag="li"
        :oo="index"
        @swiperight="rightMove"
        @swipeleft="leftMove(index)"
        :class="[('list-item'),{'left-move':(index === current)}]"
      >
        <div class="list-item-left">
          <p>{{item.note_title}}</p>
          <p>
            <span class="note-time">{{item.note_time}}</span>
            <span class="more-content" @click="clickItem">&raquo;</span>
          </p>
        </div>
        <div class="list-item-right">
          <span
            @click="deleteItem(index)"
          >删除</span>
        </div>
      </v-touch>
    </ul>
    <p class="add-list-item">
      <router-link to="/add">添加笔记</router-link>
    </p>
  </div>
</template>

<script type="text/javascript">
import store from '@/store/store'
import Menu from '@/components/menu.js'
export default {
  name: 'HomeList组件',
  data () {
    return {
      current: -1,
      last: null,
      oo: false,
      leftMark: Boolean
    }
  },
  created () {
    Menu.$emit('mark', 1)
  },
  computed: {
    pageList () {
      return store.state.list
    }
  },
  methods: {
    clickItem () {
      store.state.click_item_index = this.oo
      this.$router.push('/update')
    },
    deleteItem (value) {
      var askDelete = window.confirm('删除该条目？')
      if (askDelete) {
        store.state.list.splice(value, 1)
        alert('已删除该条目')
        this.current = -1
      }
      console.log(store.state.list)
    },
    leftMove (value) {
      if (this.last === value) { // 若连续两次均点击同一对象，通过取反，2次中必有1次重置current操作
        this.oo = !this.oo
        if (this.oo) { // 重置current为-1
          this.current = -1
          return false
        }
        this.current = value
        this.last = value // 暂存此次点击对象的编号于last
        this.leftMark = true
      }
      this.current = value
      this.last = value
      this.leftMark = true
    },
    rightMove () {
      if (this.last === value && this.leftMark === true) {
        this.current = -1 // 重置current为-1
      }
    }
  }
}
</script>

<style type="text/css" scoped>
#home-list {
  border: 1px solid green
}
.list {
  text-align: center;
}
.list-item {
  position: relative;
  display: inline-block;
  width: 92%;
  padding: 10px 8px;
  background: #e1caca;
  margin-bottom: 6px;
  border-radius: 10px;
  text-align: left;
  font: 16px/32px 'arial';
  transition: transform .3s linear;
}
.left-move {
  transform: translateX(-12%);
}
.more-content, .note-time {
  font: 14px/20px 楷体,arial;
  color: blue;
}
.more-content {
  line-height: 2px;
  float: right;
}
.list-item:hover {
  background: #4eb2df;
}
.list-item:hover .list-item-left {
  color: #fff;
}
.list-item:hover .more-content, .list-item:hover .note-time {
  color: #fff;
}
.list-item-right {
  position: absolute;
  top: 0;
  right: -13%;
  height: 100%;
  width: 12%;
  text-align: center;
  line-height: 84px;
  font-weight: 600;
  color: #a58888;
}
.add-list-item, .add-list-item a {
  display: inline-block;
}
.add-list-item {
  position: fixed;
  left: 50%;
  bottom: 10%;
  width: 60%;
  background: rgba(172, 178, 107, .5);
  transform: translateX(-50%);
  border-radius: 5px;
}
.add-list-item a {
  display: inline-block;
  width: 100%;
  height: 100%;
  font: 16px/32px 楷体;
  text-decoration: none;
}
</style>



