<template>
  <div id="app">
    <h1>Cecile'Note😏</h1>
    <p>App组件</p>
    <div class="full" @click="full">&loz;全屏&loz;</div>
    <router-view></router-view>
    <!-- <img src="./assets/logo.png"> -->
  </div>
</template>
<script>
import screenfull from 'screenfull'
export default {
  name: 'app组件',
  created () { // 每次切换组件或刷新均会执行
    if (window.Storage && window.localStorage && window.localStorage instanceof Storage) {
      // 在页面加载时读取localStorage
      if (localStorage.getItem('store')) {
        this.$store.replaceState(Object.assign({}, this.$store.state, JSON.parse(localStorage.getItem('store'))))
      }
      setInterval(() => { // 每10s把状态保存到localStorage一次
        localStorage.setItem('store', JSON.stringify(this.$store.state))
        console.log('saved')
      }, 10000)
      // 在页面刷新时将store保存到localStorage里(safari貌似不支持beforeunload)
      window.addEventListener('beforeunload', () => {
        localStorage.setItem('store', JSON.stringify(this.$store.state))
      })
    } else {
      alert('您的浏览器 不支持localStorage，将会影响使用！')
    }
  },
  methods: {
    full () {
      if (!screenfull.isEnabled) { // 判断浏览器是否支持screenfull插件
        this.$message({
          message: 'Your browser does not work',
          type: 'warning'
        })
        return false
      }
      screenfull.toggle()
    }
  }
}
</script>
<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  border: 1px solid brown;
  overflow: hidden;
}
h1 {
  font: bold 28px/42px arial;
}
.full {
  font: bold 16px/32px arial;
}
</style>



