<template>
  <div id="app">
    <c1></c1>
    <c2></c2>
  </div>
</template>

<script type="text/javascript">
import Vue from 'vue'
export default {}
var Bus = new Vue()
Vue.component('c1', {
  template: '<div>{{msg}}</div>',
  data () {
    return {
      msg: 'Hello World!'
    }
  },
  created () {
    Bus.$on('setMsg', content => {
      this.msg = content
    })
  }
})
Vue.component('c2', {
  template: '<button @click="sendEvent">Say Hi</button>',
  methods: {
    sendEvent () {
      Bus.$emit('setMsg', 'Hi Vue!')
    }
  }
})
</script>



