<template>
  <div id="about">
    <p>{{info}}</p>
    <button @click="add">点击</button>
  </div>
</template>

<script type="text/javascript">
import store from '@/store/store'
export default {
  data () {
    return {
      info: 0
    }
  },
  methods: {
    add () {
      console.log(this.info++)
      this.$store.commit('upp')
      store.commit('upp')// store是由store.js引入的一个实例
    }
  }
}
</script>

<style scoped>
#about {
  border: 1px solid red
}
</style>



