<template>
  <div id='update'>
    <p>Update组件</p>
    <p class="back">
      <span class="back-btn" @click="$router.back()">👈</span>
    </p>
    <div>
      <div>
        <input id="note-title" type="text" name="" placeholder="请输入标题"
          v-model="note_title"
        >
      </div>
      <div>
        <textarea id="note-content" name="" placeholder="请输入内容"
          v-model="note_content"
        ></textarea>
      </div>
      <p class="add-list-item">
        <button @click="submitNote">保存修改</button>
      </p>
    </div>
  </div>
</template>

<script type="text/javascript">
import store from '@/store/store'
function getNowFormatDate () {
  var date = new Date()
  var month = date.getMonth() + 1
  var strDate = date.getDate()
  var hours = date.getHours()
  var minutes = date.getMinutes()
  if (month >= 1 && month <= 9) {
    month = '0' + month
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = '0' + strDate
  }
  if (hours <= 9) {
    hours = '0' + hours
  }
  if (minutes <= 9) {
    minutes = '0' + minutes
  }
  var currentDate = date.getFullYear().toString() + '-' + month + '-' + strDate + ' ' + hours + ':' + minutes
  return currentDate
}
export default {
  name: 'update组件',
  data () {
    return {
      note_title: store.state.list[store.state.click_item_num].note_title,
      note_content: store.state.list[store.state.click_item_num].note_content,
      note_time: store.state.list[store.state.click_item_num].note_time
    }
  },
  // created () { // 登录状态验证
  //   if (store.state.status === false) {
  //     this.$router.push('/')
  //   }
  // },
  methods: {
    submitNote () {
      var updateTime = window.confirm('是否要保存为 最新时间？')
      if (updateTime) {
        this.note_time = getNowFormatDate()
      } else {
        alert('再见啦！')
      }
      // 将更新后的数据写入store
      store.commit('updateItem', {
        note_title: this.note_title,
        note_content: this.note_content,
        note_time: this.note_time
      })
      // localStorage.setItem('store', JSON.stringify({'note_title': this.note_title, 'note_content': this.note_content}))
    }
  }
}
</script>

<style type="text/css" scoped>
#update {
  border: 1px solid black;
}
#note-title, #note-content {
  font: 16px/24px 楷体;
  width: 90%;
  border: 1px solid blue;
}
#note-title:focus, #note-content:focus {
  outline: none;
}
#note-title {
  border-width: 0 0 1px;
}
#note-content {
  height: 300px;
  border-width: 0 0;
}
.add-list-item {
  position: fixed;
  left: 50%;
  bottom: 10%;
  width: 60%;
  transform: translateX(-50%);
}
.add-list-item button {
  display: inline-block;
  width: 100%;
  height: 100%;
  background: rgba(172, 178, 107, .5);
  border-radius: 5px;
  border-width: 0;
  font: 16px/32px 楷体;
  text-decoration: none;
}
.back {
  position: relative;
  height: 48px;
}
.back-btn {
  position: absolute;
  top: 0;
  left: 4%;
  font: bold 16px/24px 楷体,arial;
  color: red;
}
</style>



