<template>
  <div id="home-user">
    <p>HomeUser组件</p>
    <img src="@/assets/nezha.png">
    <p>切换组件后，组建之中的数据会及时更新</p>
    <p>{{msg}}</p>
    <p class="add-list-item">
      <button @click="deleteAll">先清除store，再清除localS</button>
      <button @click="quit">退出登录</button>
    </p>
  </div>
</template>

<script type="text/javascript">
import store from '@/store/store'
import Menu from '@/components/menu.js'
export default {
  name: 'home组件',
  data () {
    return {
      msg: store.state.count,
      mark: 2
    }
  },
  created () {
    Menu.$emit('mark', 2)
  },
  methods: {
    deleteAll () {
      store.commit('clearAll')
    },
    quit () {
      store.commit('changeStatus')
      this.$router.push('/')
    }
  }
}
</script>

<style type="text/css" scoped>
#home-user {
  border: 1px solid brown
}
img {
  margin: 6% 0;
}
.add-list-item {
  position: fixed;
  left: 50%;
  bottom: 10%;
  width: 60%;
  transform: translateX(-50%);
}
.add-list-item button {
  display: inline-block;
  width: 100%;
  height: 100%;
  background: rgba(172, 178, 107, .5);
  border-radius: 5px;
  border-width: 0;
  font: 16px/32px 楷体;
  text-decoration: none;
}
.add-list-item button:nth-child(1) {
  margin-bottom: 10%;
}
</style>



